"use client";
import { useState } from "react";
import Image from "next/image";
import styles from "../recommender/recommender.module.css";
import StyledButton from "./StyledButton";
import Modal from "./Modal";
import useRecommendation from "../about/hooks/useRecommendation";
type Movie = {
  id: number;
  title: string;
  posterUrl: string;
  year: number;
};

export default function Recommender() {
  const [movies, setMovies] = useState<Movie[]>([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [searchQuery, setSearchQuery] = useState("");
  const { recommendations, isLoading, error } = useRecommendation(searchQuery); // Use the hook here

  const handleSearchMovie = () => {
    // This will now trigger the hook to fetch recommendations based on searchQuery
    if (searchQuery) {
      setMovies(Array.isArray(recommendations) ? recommendations : []); // Ensure recommendations is an array
      setIsModalOpen(true);
    }
  };

  const handleGetNewRecommendations = () => {
    // Mock data - replace with real API call later
    setMovies([
      {
        id: 1,
        title: "The Shawshank Redemption",
        posterUrl:
          "https://image.tmdb.org/t/p/w500/q6y0Go1tsGEsmtFryDOJo3dEmqu.jpg",
        year: 1994,
      },
      {
        id: 2,
        title: "The Godfather",
        posterUrl:
          "https://image.tmdb.org/t/p/w500/3bhkrj58Vtu7enYsRolD1fZdja1.jpg",
        year: 1972,
      },
      {
        id: 3,
        title: "The Dark Knight",
        posterUrl:
          "https://image.tmdb.org/t/p/w500/qJ2tW6WMUDux911r6m7haRef0WH.jpg",
        year: 2008,
      },
    ]);
    setIsModalOpen(true);
  };

  return (
    <div className={styles.page}>
      <main className={styles.main}>
        <div className={styles.ctas}>
          <h1 className={styles.title}>Movie Recommendations</h1>

          {movies.length === 0 ? (
            <div className={styles.buttonContainer}>
              <StyledButton
                variant="primary"
                onClick={handleGetNewRecommendations}
              >
                Find Movies
              </StyledButton>
            </div>
          ) : (
            <>
              <div className={styles.movieGrid}>
                {movies.map((movie) => (
                  <div key={movie.id} className={styles.movieCard}>
                    <Image
                      src={movie.posterUrl}
                      alt={`${movie.title} poster`}
                      width={200}
                      height={300}
                      style={{
                        width: "100%",
                        height: "auto",
                        borderRadius: "8px",
                      }}
                    />
                    <h3 className={styles.movieTitle}>{movie.title}</h3>
                    <span className={styles.movieYear}>{movie.year}</span>
                  </div>
                ))}
              </div>
              <div className={styles.searchContainer}>
                <input
                  type="text"
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                />
              </div>
              <div className={styles.buttonContainer}>
                <StyledButton variant="secondary" onClick={handleSearchMovie}>
                  Get Recommendations
                </StyledButton>
              </div>
            </>
          )}
        </div>
      </main>
      <footer className={styles.footer}></footer>

      <Modal isOpen={isModalOpen} onClose={() => setIsModalOpen(false)}>
        <div>
          <h1>Hello</h1>
          <StyledButton
            variant="secondary"
            onClick={() => setIsModalOpen(false)}
          >
            Close
          </StyledButton>
        </div>
      </Modal>
    </div>
  );
}
